<template>
  <div class="recommend-container">
    <ul class="recommend"  v-if="recommendshoplist.length>0 && userInfo.id">
      <ShopList tag="li"
                v-for="(item, index) in recommendshoplist"
                :key="index"
                :item="item"
                :clickCellBtn = 'dealWatchBtnClick'
      />
    </ul>
    <SelectLogin v-else/>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import ShopList from '@/components/ShopList/ShopList'
  import BScroll from 'better-scroll'
  import {Indicator,Toast} from 'mint-ui';
  import SelectLogin from '@/pages/Me/Login/SelectLogin'
  import {addGoods2Car} from "@/api/index"

  export default {
    name: "Recommend",
    data() {
      return {
        page: 1,
        count: 10,
        top: 0,
        status: true
      }
    },
    components: {
      ShopList,
      SelectLogin
    },
    mounted() {
      Indicator.open('...');
      // 
      this.$store.dispatch('reqRecommendShopList', {
        page: this.page, count: this.count, callback: () => {
          Indicator.close();
        }
      })
    },
    watch: {
      recommendshoplist() {
        this.$nextTick(() => {
          // +1
          this.page += 1;
          // 
          this._initBScroll()
          // 
          this.listScroll.scrollTo(0, this.top)
        })
      }
    },
    methods: {
      _initBScroll() {
        //1.1
        this.listScroll = new BScroll('.recommend-container', {
          scrollY: true,
          probeType: 3
        })
        // 1.2
        this.listScroll.on('touchEnd', (pos) => {
          // 1.2.1 
          if (pos.y >= 50) {
            console.log('')
          }
          //1.2.2 
          if (this.status === true) {
            if (this.listScroll.maxScrollY > pos.y + 30) {
              this.status = false;
              Indicator.open('...');
              // 
              this.top = this.listScroll.maxScrollY - 50
              // 
              this.$store.dispatch('reqRecommendShopList', {
                page: this.page, count: this.count, callback: () => {
                  Indicator.close();
                  this.status = true;
                }
              })
            }
          }
        })
        // 
        console.log(this.top)
        // 1.3
        this.listScroll.on('scrollEnd', () => {
          this.listScroll.refresh();
        })
      },
      // 
      async dealWatchBtnClick(goods) {
        // 1. 
        let result = await addGoods2Car(this.userInfo.id,goods.goods_id,goods.goods_name,goods.thumb_url,goods.price);
        //console.log(result)
        Toast("")
        this.$store.dispatch('getCartGoods');
      }
    },
    computed: {
      ...mapState(['recommendshoplist','userInfo'])
    }
  }
</script>

<style scoped lang="stylus">
  .recommend-container
    width: 100%
    height: 100%
    background #f5f5f5
    overflow hidden
    .recommend
      padding-bottom 50px
      display flex
      flex-wrap wrap
      background #f5f5f5
</style>



